import { useState } from "react";
import MusicPlayer from "./shared/MusicPlayer/MusicPlayer";

import "./App.scss";

function App() {
  const [isHelperVisible, SetHelperVisibility] = useState(true);

  function musicPlayerHandler(data: "play" | "pause" | "stop") {
    if (data === "play" || data === "pause") {
      SetHelperVisibility(false);
    } else {
      SetHelperVisibility(true);
    }
  }
  const musicHelperClassName = isHelperVisible ? "bounce" : "fade-out";

  return (
    <div className="App">
      <div className="bg-image" />
      <div className="bg-opacity" />
      <h1>Welcome~</h1>
      <span className="description">
        <span
          className="name-link"
          onClick={() =>
            window.open("https://www.twitter.com/theminifelix", "_blank")
          }
        >
          MiniFelix's
        </span>{" "}
        house üè† is currently under construction
        <span className="dot">.</span>
        <span className="dot">.</span>
        <span className="dot">.</span>
      </span>

      <div className="music-player">
        <div className={musicHelperClassName}>
          <h1>Play Me!</h1>
        </div>
        <MusicPlayer onActionCallback={musicPlayerHandler} />
      </div>
    </div>
  );
}

export default App;
