import React, { useEffect, useState } from "react";
import "./MusicPlayer.scss";

import bgAudio from "../../assets/audio/drifting_away.mp3";

import IconButton from "@material-ui/core/IconButton";

import PlayIcon from "@material-ui/icons/PlayArrow";
import PauseIcon from "@material-ui/icons/Pause";
import StopIcon from "@material-ui/icons/Stop";

type MusicPlayerProps = {
  onActionCallback: (action: "play" | "pause" | "stop") => void;
};

const MusicPlayer: React.FC<MusicPlayerProps> = ({ onActionCallback }) => {
  const audio = new Audio(bgAudio);
  const [audioState, setAudioState] = useState({
    state: "pause",
    audio: audio,
    canStop: false,
  });

  useEffect(() => {
    audioState.audio.load();
    audioState.audio.loop = true;
  }, []);

  const Play = () => {
    audioState.audio.volume = 0.1;
    audioState.audio.play();
    setAudioState({
      ...audioState,
      state: "play",
      canStop: true,
    });

    onActionCallback("play");
  };
  const Pause = () => {
    audioState.audio.pause();
    setAudioState({
      ...audioState,
      state: "pause",
      canStop: true,
    });

    onActionCallback("pause");
  };

  const Stop = () => {
    audioState.audio.pause();
    audioState.audio.currentTime = 0;
    setAudioState({ ...audioState, state: "pause", canStop: false });
    onActionCallback("stop");
  };

  const authorClassName = !audioState.canStop
    ? "audio-name fade-out"
    : "audio-name fade-in";

  return (
    <div className="MusicPlayer">
      <p className={authorClassName}>
        <span
          className="author"
          onClick={() =>
            window.open("https://soundcloud.com/albielofi", "_blank")
          }
        >
          Albie
        </span>{" "}
        - Drifting Away
      </p>
      {audioState.state === "pause" ? (
        <IconButton className="icon-button" onClick={() => Play()}>
          <PlayIcon fontSize="large" className="icon" />
        </IconButton>
      ) : (
        <IconButton className="icon-button" onClick={() => Pause()}>
          <PauseIcon fontSize="large" className="icon" />
        </IconButton>
      )}
      <IconButton
        className="icon-button"
        disabled={!audioState.canStop}
        onClick={() => Stop()}
      >
        <StopIcon fontSize="large" />
      </IconButton>
    </div>
  );
};

export default MusicPlayer;
